<template>
  <div class="d-flex flex-column align-items-center">
    <h1 class="my-5">Iniciar sesión</h1>

    <form class="d-flex flex-column" @submit.prevent="comprobarCrecenciales()">
      <div class="">
        <div id="form" class="px-5 py-3">
          Usuario
          <input
            type="text"
            class="form-control my-2"
            placeholder="Usuario"
            required
            v-model="usuario.usuario"
          />
          Contraseña
          <input
            type="password"
            class="form-control my-2"
            placeholder="Contraseña"
            required
            v-model="usuario.password"
          />
        </div>
        <b-button id="enviar" pill class="my-4 w-100" type="submit">
          Iniciar
        </b-button>
      </div>
    </form>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
  </div>
</template>

<script>
import app from './App'
export default {
  data() {
    return {
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      usuario: {
        usuario: '',
        password: '',
      },
    }
  },
  created() {
    if (localStorage.getItem('id') != null) {
      this.$router.go(-1)
    }
  },

  methods: {
    alertaUsuario() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'El usuario introducido no existe'
      this.showAlert()
    },
    alertaPass() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'La contraseña y usuario son incorrectos'
      this.showAlert()
    },
    comprobarCrecenciales() {
      this.axios
        .post('/usuarios/comprobarUsuario', this.usuario)
        .then((res) => {
          if (res.data.existe) {
            this.axios
              .post('/usuarios/inicioSesion', this.usuario)
              .then((res) => {
                if (res.data.inicioFallido) {
                  this.alertaPass()
                } else {
                  localStorage.setItem('id', res.data.id)
                  localStorage.setItem('nombre', res.data.nombre)
                  localStorage.setItem('usuario', res.data.usuario)
                  localStorage.setItem('tatuador', res.data.tatuador)
                  location.reload()
                }
              })
              .catch((e) => {
                console.log(e)
              })
          } else {
            this.alertaUsuario()
          }
        })
        .catch((e) => {
          console.log(e)
        })
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
      if (this.mensaje.color == 'success') {
        setTimeout(() => {
          this.$router.go(-1)
        }, 500)
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
  },
  mounted() {
    document.querySelector('#form > input:nth-child(1)').focus()
  },
}
</script>

<style>
#form {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 20px;
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
#enviar {
  background-color: #82a5c7;
  color: #243241;
}
</style>
